import { useState, useEffect, useRef } from 'react';

// Settings Components
import { BplMediaPlaceholder } from '../../../../Components';

import Settings from './Settings/Settings';
import Style from '../Common/Style';
import { getExtension } from '../../utils/functions';
import { plyrConfig } from '../../utils/config';
import { cameraIcon } from '../../utils/icons';

const Edit = props => {
	const { className, attributes, setAttributes, clientId } = props;
	const { source, poster, controls, repeat, autoplay, muted, resetOnEnd, autoHideControl } = attributes;

	useEffect(() => { clientId && setAttributes({ clientId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to clientId

	const videoWrapper = useRef(null);
	const videoElement = useRef(null);
	const [autoplayProps, setAutoplayProps] = useState(autoplay ? { autoplay } : {});
	const [mutedProps, setMutedProps] = useState(muted ? { muted } : {});

	useEffect(() => {
		if (source) {
			videoWrapper.current.innerHTML = '';
			videoWrapper.current.innerHTML = videoElement.current.outerHTML;

			const player = new Plyr(videoWrapper.current.children[0], plyrConfig(attributes));

			player.on('ready', () => {
				if (muted && autoplay) {
					player.play();
				}
			});
		}

		autoplay ? setAutoplayProps({ autoplay }) : {}
		muted ? setMutedProps({ muted }) : {}
	}, [videoWrapper, source, poster, controls, repeat, autoplay, muted, resetOnEnd, autoHideControl]);

	return source ? <>
		<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} />

		<div className={className} id={`vpbVideoPlayer-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			<div className='vpbVideoPlayer'>
				<div className='videoWrapper' ref={videoWrapper}></div>

				{/* eslint-disable-next-line react/no-unknown-property */}
				<video controls playsinline data-poster={poster} preload='metadata' {...autoplayProps} {...mutedProps} ref={videoElement}>
					Your browser does not support the video tag.
					<source src={source} type={`video/${getExtension(source) || 'mp4'}`} />
				</video>
			</div>
		</div>
	</> : <BplMediaPlaceholder type='video' onChange={val => setAttributes({ source: val.url })} icon={cameraIcon} />;
};
export default Edit;